.page-container {
    padding: 24px;
    font-family: sans-serif;
    background-color: #f9fafb; /* light gray */
    min-height: 100vh;
}

/* Card styles */
.exp_card {
    background: white;
    border-radius: 8px;
    padding: 24px;
    box-shadow: 0 1px 2px rgba(0,0,0,0.1);
}

.exp_card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;
}

.exp_card-header h2 {
    font-size: 20px;
    font-weight: 700;
}

.add-button {
    display: flex;
    align-items: center;
    gap: 6px;
    background: #2563eb;
    color: white;
    border: none;
    border-radius: 6px;
    padding: 8px 14px;
    font-size: 14px;
    cursor: pointer;
}

.add-button:hover {
    background: #1d4ed8;
}

/* Empty state */
.empty-state {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #6b7280;
    gap: 12px;
    text-align: center;
}

.empty-state h3 {
    color: #111827;
    font-size: 16px;
    font-weight: 600;
    margin: 0;
}

.empty-state p {
    margin: 0;
    font-size: 14px;
}

.primary-button {
    background: #2563eb;
    color: white;
    border: none;
    border-radius: 6px;
    padding: 8px 16px;
    font-size: 14px;
    cursor: pointer;
    margin-top: 12px;
}

.primary-button:hover {
    background: #1d4ed8;
}

.expense-types-container {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-top: 20px;
}

.expense-type-card {
  display: flex;
  align-items: center;
  background: white;
  padding: 14px 18px;
  border-radius: 10px;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.04);
  border: 1px solid #eaeaea;
  transition: box-shadow 0.2s;
}

.expense-type-card:hover {
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.06);
}

.icon-circle {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 16px;
}

.expense-type-info {
  flex: 1;
}

.expense-type-name {
  font-weight: 600;
  font-size: 16px;
}

.expense-type-date {
  font-size: 13px;
  color: #777;
}

.expense-type-actions {
  display: flex;
  gap: 10px;
}

.icon-button {
  background: none;
  border: none;
  color: #666;
  font-size: 14px;
  cursor: pointer;
  transition: color 0.2s;
}

.icon-button:hover {
  color: #000;
}

